.Listitems{
    color: #ffffff;
    background-color: coral;
    margin: 20px auto;
    width: 320px;
    height: 45px;
    border-radius: 5px;
    border: coral;
}
.Listitems p{
    padding: 10px;
    position: relative;
}
.Listitems p span {
    position: absolute;
    right: 10px;
}
.Listitems input {
    background-color: transparent;
    border: none;
    color: #ffffff;
}
.Listitems input:focus {
    outline: none;
}
.faicons{
    margin: 0 5px;
    font-size: 15px;
}