body{
  background-color: white;
}

.App{
  background-color: yellowgreen;
  height: 500px;
  width: 400px;
  margin: 40px auto;
}
#to_do input{
  background-color: transparent;
  border: none;
  border-bottom:1px solid #ffffff;
  height: 50px;
  margin: 20px;
  font-size: 18px;
  color: #ffffff;
}
#to_do input::placeholder{
  color: #ffffff;
}
#to_do input:focus, #to_do button:focus{
  outline: none;
}
#to_do button{ 
  height: 50px;
  font-size: 18px;
  border: none;
  border-radius: 5px;
  background-color: coral;
  color: #ffffff;
  padding: 10px;
  font-weight: 400;
  width: 100px;
}